.watchlistHeading{
    text-align: center;

    color: white;
}

.watchlistContainer{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 50px;
}

.watchlistMovieCard{
    background-color: rgba(255, 255, 255, 0.178);
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 250px;
    margin: 25px;
    border-radius: 5px;
    box-shadow: 0 0 5px 2px lightgray;
}

.watchlistMovieCover{
    height: 350px;
    width: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;}

.watchlistMovieName{
    text-align: center;
    color: white;
    margin: 0 5px 5px 5px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-weight: bold;
}

.watchlistMovieBottomInfo{
    color: white;
    display: flex;
    justify-content: space-between;
}


.no-movies{
    text-align: center;
    color: white;
}